er sm:block sm:p-0\">\n                            <div class=\"fixed inset-0 transition-opacity\" aria-hidden=\"true\">\n                                <div class=\"absolute inset-0 bg-gray-500 opacity-75\"></div>\n                            </div>\n\n                            <span class=\"hidden sm:inline-block sm:align-middle sm:h-screen\"\n                                aria-hidden=\"true\">&#8203;</span>\n                            <div class=\"inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-lg sm:w-full\"\n                                role=\"dialog\" aria-modal=\"true\" aria-labelledby=\"modal-headline\">\n                                <div class=\"bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4\">\n                                    <div class=\"sm:flex sm:items-start\">\n                                        <div\n                                            class=\"mx-auto flex-shrink-0 flex items-center justify-center h-12 w-12 rounded-full bg-red-100 sm:mx-0 sm:h-10 sm:w-10\">\n                                            <!-- Heroicon name: outline/exclamation -->\n                                            <!-- <svg class=\"h-6 w-6 text-red-600\" xmlns=\"http://www.w3.org/2000/svg\" fill=\"none\" viewBox=\"0 0 24 24\" stroke=\"currentColor\" aria-hidden=\"true\">\n                                                <path stroke-linecap=\"round\" stroke-linejoin=\"round\" stroke-width=\"2\" d=\"M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z\" />\n                                            </svg> -->\n\n                                            <icon :class=\"'h-6 w-6 text-green-600'\" :name=\"'folder'\">\n                                            </icon>\n                                        </div>\n                                        <div class=\"mt-3 text-center sm:mt-0 sm:ml-4 sm:text-left\">\n                                            <h3 class=\"text-lg leading-6 font-medium text-gray-900\" id=\"modal-headline\">\n                                                File Upload\n                                            </h3>\n                                            <div class=\"mt-2\">\n                                                <input type=\"file\" multiple\n                                                    class=\"shadow appearance-none border rounded py-2 px-3 text-black\"\n                                                    id=\"file\" ref=\"file\" @change=\"handleFileUpload()\" />\n                                            </div>\n                                        </div>\n                                    </div>\n                                </div>\n                                <div class=\"bg-gray-50 px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse\">\n                                    <button type=\"button\"\n                                        class=\"mt-3 w-full inline-flex justify-center rounded-md border border-gray-300 shadow-sm px-4 py-2 bg-white text-base font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 sm:mt-0 sm:ml-3 sm:w-auto sm:text-sm\"\n                                        @click=\"addFileModal()\">\n                                        Cancel\n                                    </button>\n                                </div>\n                            </div>\n                        </div>\n                    </div>\n                    <!-- End add file modal -->\n                </div>\n            </div>\n        </div>\n        <!-- end header -->\n        <!-- categories -->\n        <!-- <div class=\"mt-5 flex flex-row px-5\">\n            <span\n                class=\"px-5 py-1 bg-yellow-500 rounded-2xl text-white text-sm mr-4\"\n                >\n            All items\n            </span>\n            <span class=\"px-5 py-1 rounded-2xl text-sm font-semibold mr-4\">\n            Food\n            </span>\n            <span class=\"px-5 py-1 rounded-2xl text-sm font-semibold mr-4\">\n            Cold Drinks\n            </span>\n            <span class=\"px-5 py-1 rounded-2xl text-sm font-semibold mr-4\">\n            Hot Drinks\n            </span>\n        </div> -->\n        <!-- end categories -->\n        <!-- Media list -->\n        <div\n            class=\"flex w-full grid-flow-row grid-cols-4 items-center gap-4 overflow-y-hidden overflow-x-scroll px-10 pt-1 pb-10 xl:grid xl:overflow-x-auto xl:px-4 svelte-1n6ue57\">\n            <div v-for=\"(item, index) in file\" :key=\"index\">\n\n                <div class=\"card card-compact w-96 bg-base-100 shadow-xl\">\n                    <!-- If is image -->\n                    <div v-if=\"extension.includes(item.ext)\">\n                        <figure>\n                            <image-edit @loading=\"loading\" @load_file=\"loadFiles\" v-bind:item=\"item\" />\n                        </figure>\n                    </div>\n                    <!-- not editable files -->\n                    <div v-else>\n                        <svg xmlns=\"http://www.w3.org/2000/svg\" class=\"w-full\" fill=\"none\" viewBox=\"0 0 24 24\"\n                            stroke=\"currentColor\" stroke-width=\"2\">\n                            <path stroke-linecap=\"round\" stroke-linejoin=\"round\"\n                                d=\"M7 21h10a2 2 0 002-2V9.414a1 1 0 00-.293-.707l-5.414-5.414A1 1 0 0012.586 3H7a2 2 0 00-2 2v14a2 2 0 002 2z\" />\n                        </svg>\n                    </div>\n                    <div class=\"card-body\">\n                        <h2 class=\"card-title\">{{ item.name }}</h2>\n                        <p>{{ item.ext }}</p>\n                        <div class=\"card-actions justify-end\">\n                            <div class=\"badge badge-outline\">\n                                <a target=\"_blank\" :href=\"item.url['default']\">Download</a>\n                            </div>\n                            <div class=\"badge badge-outline\">{{ item.media_size }}</div>\n                            <edit-assistant-media @load_folder=\"loadFiles\" v-bind:item=\"item\" />\n                        </div>\n                    </div>\n                </div>\n            </div>\n        </div>\n        <!-- end media list -->\n\n        <!-- Loading  -->\n        <!-- v-if=\"is_loading\" -->\n        <div v-if=\"is_loading\"\n            class=\"fixed top-0 left-0 right-0 bottom-0 w-full h-screen z-50 overflow-hidden bg-black opacity-75 flex flex-col items-center justify-center\">\n            <div class=\"loader ease-linear rounded-full border-4 border-t-4 border-gray-200 h-12 w-12 mb-4\"></div>\n            <h2 class=\"text-center text-white text-xl font-semibold\">\n                Loading...\n            </h2>\n            <p class=\"w-1/3 text-center text-white\">\n                This may take a few seconds, please don't close this page.\n            </p>\n        </div>\n    </div>\n</template>\n<script>\nexport default {\n    name: \"media-content\",\n    props: {\n        parent_id: {\n            type: Number,\n            default: null,\n        },\n        extension: {\n            type: Array,\n            default: [\"jpeg\", \"jpg\", \"png\", \"gif\", \"webp\"],\n        },\n    },\n    data: function () {\n        return {\n            file: [],\n            add_modal_file_enable: false,\n            is_loading: false,\n        };\n    },\n    methods: {\n        showModal() {\n            this.unityToast(\"Drag and drop your file\");\n        },\n        async uploadFile(fileRef) {\n            let formData = new FormData();\n            formData.append(\"file\", fileRef);\n            if (this.parent_id == null) {\n                this.unityToast(\"Select a folder\");\n            } else {\n                await axios\n                    .post(\"/file/upload/\" + this.parent_id, formData, {\n                        headers: {\n                            \"Content-Type\": \"multipart/form-data\",\n                        },\n                    })\n                    .catch((error) => {\n                        if (error.response) {\n                            for (const [key, value] of Object.entries(\n                                error.response.data.errors\n                            )) {\n                                this.unityToast(value);\n                            }\n                        }\n                    });\n            }\n        },\n        async handleFileUpload() {\n            // Reference to the files\n            this.loading();\n            for (const [key, value] of Object.entries(this.$refs.file.files)) {\n                await this.uploadFile(value);\n            }\n            this.loadFiles();\n            this.loading();\n            addFileModal();\n        },\n        async drangAndDropFile(e) {\n            let droppedFiles = e.dataTransfer.files;\n            if (!droppedFiles) return;\n            // this tip, convert FileList to array, credit: https://www.smashingmagazine.com/2018/01/drag-drop-file-uploader-vanilla-js/\n            this.loading();\n            for (const [key, value] of Object.entries(droppedFiles)) {\n                await this.uploadFile(value);\n            }\n            this.loadFiles();\n            this.loading();\n        },\n        async loadFiles() {\n            if (this.parent_id || this.parent_id === 0) {\n                this.loading();\n                await axios\n                    .get(\"/folder/files/\" + this.parent_id, {})\n                    .then((response) => {\n                        this.file = response.data.data;\n                    })\n                    .catch(function (error) { });\n                this.loading();\n            }\n        },\n        loading() {\n            if (this.is_loading) {\n                this.is_loading = false;\n            } else {\n                this.is_loading = true;\n            }\n        },\n        // Modal to that add files\n        addFileModal() {\n            if (this.add_modal_file_enable) {\n                this.add_modal_file_enable = false;\n            } else {\n                this.add_modal_file_enable = true;\n            }\n        },\n    },\n    watch: {\n        // On prop change we load the files lis\n        parent_id: function (val) {\n            if (val === null) {\n                this.file = [];\n            } else {\n                this.loadFiles();\n            }\n        },\n    },\n    created() { },\n    computed: {},\n    mounted() { },\n};\n</script>\n<style>\n.loader {\n    border-top-color: #000;\n    -webkit-animation: spinner 1.5s linear infinite;\n    animation: spinner 1.5s linear infinite;\n}\n\n@-webkit-keyframes spinner {\n    0% {\n        -webkit-transform: rotate(0deg);\n    }\n\n    100% {\n        -webkit-transform: rotate(360deg);\n    }\n}\n\n@keyframes spinner {\n    0% {\n        transform: rotate(0deg);\n    }\n\n    100% {\n        transform: rotate(360deg);\n    }\n}\n</style>\n"],"sourceRoot":""}]);
